export default "/* Dropdown Button */\n/*.dropbtn {*/\n/*background-color: darkslategray;*/\n/*color: white;*/\n/*padding: 16px;*/\n/*font-size: 16px;*/\n/*border: none;*/\n/*cursor: pointer;*/\n/*}*/\n/*!* The container <div> - needed to position the dropdown content *!*/\n/*.dropdown {*/\n/*position: relative;*/\n/*display: inline-block;*/\n/*}*/\n/*!* Dropdown Content (Hidden by Default) *!*/\n/*.dropdown-content {*/\n/*display: none;*/\n/*position: absolute;*/\n/*background-color: darkslategray;*/\n/*min-width: 160px;*/\n/*box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);*/\n/*z-index: 1;*/\n/*border: 10px;*/\n/*border-color: white;*/\n/*}*/\n/*!* Links inside the dropdown *!*/\n/*.dropdown-content a {*/\n/*color: white;*/\n/*padding: 12px 16px;*/\n/*text-decoration: none;*/\n/*display: block;*/\n/*box-shadow: 2px 2px 2px 2px rgba(255,255,255,0.2);*/\n/*}*/\n/*!* Change color of dropdown links on hover *!*/\n/*.dropdown-content a:hover {background-color: lightsteelblue}*/\n/*!* Show the dropdown menu on hover *!*/\n/*.dropdown:hover .dropdown-content {*/\n/*display: block;*/\n/*}*/\n/*!* Change the background color of the dropdown button when the dropdown content is shown *!*/\n/*.dropdown:hover .dropbtn {*/\n/*background-color: lightsteelblue;*/\n/*}*/\n.menu-bar {\n  margin: 10px 0;\n}\n/*first level*/\n.menu-bar > div {\n  position: relative;\n  display: inline-block;\n}\n.menu-bar button {\n  background-color: darkslategray;\n  color: white;\n  padding: 8px 25px;\n  font-size: 16px;\n  border: none;\n  cursor: pointer;\n}\n/*second level*/\n.menu-bar .sub-menu-item {\n  display: none;\n  position: absolute;\n  background-color: darkslategray;\n  min-width: 180px;\n  z-index: 1;\n}\n.menu-bar .sub-menu-item a {\n  color: white;\n  padding: 12px 16px;\n  text-decoration: none;\n  display: block;\n  border-bottom: 1px solid #4b6350;\n}\n.menu-bar .sub-menu-item a:hover {\n  background-color: #3d6a6a;\n}\n.menu-bar > div:hover .sub-menu-item {\n  display: block;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"